export const GET_ALL_ARTS_SUCCESS = 'GET_ALL_ARTS_SUCCESS';
export const GET_ALL_ARTS_ERROR = 'GET_ALL_ARTS_ERROR';

export const GET_ART_REQUEST = 'GET_ART_REQUEST';
export const GET_ART_SUCCESS = 'GET_ART_SUCCESS';
export const GET_ART_ERROR = 'GET_ART_ERROR';

export const CREATE_ART_REQUEST = 'CREATE_ART_REQUEST';
