import axios from "axios";

const baseURL = `http://localhost:8080/api`;

// Artists
async function getAllArtists() {
  const url = `${baseURL}/artists/all`;
  return await axios
    .get(url, { headers: { "Content-Type": "application/json" } })
    .then((response) => response)
    .catch((error) => error);
}

async function getSingleArtist(data) {
  const url = `${baseURL}/artists/single?artist_id=${data.artist_id}`;
  return await axios
    .get(url, { headers: { "Content-Type": "application/json" } })
    .then((response) => response)
    .catch((error) => error);
}

async function createArtist(data) {
  const url = `${baseURL}/artists/create`;
  return await axios
    .post(url, data, { headers: { "Content-Type": "application/json" } })
    .then((response) => response)
    .catch((error) => error);
}

