import * as yup from "yup";
import { useFormik } from "formik";
import React, { useEffect } from "react";
import { connect } from "react-redux";
import artistActions from "../../redux/actions/artistActions";

function ArtistCreate(props) {
  const validationSchema = yup.object().shape({
    artist_name: yup.string().required("Name is required"),
    artist_description: yup.string().required("Description is required"),
    artist_image: yup.string().required("Image is required"),
  });
  const formik = useFormik({
    initialValues: {
      artist_name: "",
      artist_description: "",
      artist_name_ar: "",
      artist_description_ar: "",
      artist_image: "",
    },
    validationSchema: validationSchema,
    onSubmit: (values) => {
      props.createArtist(values);
    },
  });
  useEffect(() => {
    props.clearArtistMessage();
  }, []);

  return (
    <div>
      <form
        onSubmit={formik.handleSubmit}
        style={{ fontSize: "30px", margin: "auto", width: "50%" }}
      >
        <div>Artist Name</div>
        <input
          type="text"
          name="artist_name"
          width="100%"
          style={{ fontSize: 30, width: "100%" }}
          value={formik.values.artist_name}
          onChange={formik.handleChange}
        />
        <div>Artist Description</div>
        <textarea
          name="artist_description"
          rows="10"
          style={{ fontSize: 20, width: "100%" }}
          value={formik.values.artist_description}
          onChange={formik.handleChange}
        />
        <div>Artist Name Arabic</div>
        <input
          type="text"
          name="artist_name_ar"
          width="100%"
          style={{ fontSize: 30, width: "100%" }}
          value={formik.values.artist_name_ar}
          onChange={formik.handleChange}
        />
        <div>Artist Description Arabic</div>
        <textarea
          name="artist_description_ar"
          rows="10"
          style={{ fontSize: 20, width: "100%" }}
          value={formik.values.artist_description_ar}
          onChange={formik.handleChange}
        />
        <div>Artist Image</div>
        <input
          type="text"
          name="artist_image"
          width="100%"
          style={{ fontSize: 30, width: "100%" }}
          value={formik.values.artist_image}
          onChange={formik.handleChange}
        />
        <button type="submit" disabled={props.createArtistLoading}>
          Submit
        </button>
        {props.createArtistMessage && <div>{props.createArtistMessage}</div>}
        {formik.errors && (
          <div style={{ color: "red" }}>
            <div>{formik.errors.artist_name}</div>
            <div>{formik.errors.artist_description}</div>
            <div>{formik.errors.artist_image}</div>
          </div>
        )}
      </form>
    </div>
  );
}

const mapStateToProps = (state) => ({
  createArtistMessage: state.artist.createArtistMessage,
  createArtistLoading: state.artist.createArtistLoading,
});

const mapDispatchToProps = (dispatch) => ({
  clearArtistMessage: () => dispatch(artistActions.clearArtistMessage()),
  createArtist: (data) => dispatch(artistActions.createArtist(data)),
});

export default connect(mapStateToProps, mapDispatchToProps)(ArtistCreate);
