import { Grid } from "@material-ui/core";
import React, { useEffect } from "react";
import ArtistCard from "../components/common/ArtistCard";
import { useHistory } from "react-router";
import artistActions from "../redux/actions/artistActions";
import { connect } from "react-redux";
// IMAGES
import ArtImage from "../images/Arts/painting Fadi Alawayid.jpg";

function Artists(props) {
  const history = useHistory();
  useEffect(() => {
    props.getAllArtists();
  }, []);
  return (
    <>
      {props.getAllArtistsLoading ? (
        <div>loading</div>
      ) : (
        <Grid container style={{ position: "relative" }}>
          {props?.getAllArtistsData?.length > 0 &&
            props?.getAllArtistsData?.map((zone, i) => {
              return (
                <Grid key={`art-${i}`} item xs={6} style={{}}>
                  <ArtistCard
                    image={zone.artist_image}
                    artist_name={zone.artist_name}
                    onClick={() => history.push(`/artist/${zone._id}`)}
                  />
                </Grid>
              );
            })}
        </Grid>
      )}
    </>
  );
}

const mapStateToProps = (state) => ({
  getAllArtistsData: state.artist.getAllArtistsData,
  getAllArtistsLoading: state.artist.getAllArtistsLoading,
});

const mapDispatchToProps = (dispatch) => ({
  getAllArtists: () => {
    dispatch(artistActions.getAllArtists());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(Artists);
