import React from "react";
import { useHistory } from "react-router";
import Logo from "../../images/khawla_logo_onscroll.png";

function Header(props) {
  const history = useHistory();
  return (
    <div
      style={{
        top: 0,
        left: 0,
        right: 0,
        height: props.isOpen ? "10vh" : "0vh",
        backgroundColor: "white",
        borderBottom: "1px solid #A32B5F",
        color: "white",
        position: "fixed",
        transition: "1000ms all",
        zIndex: "3",
      }}
    >
      <div style={{}}>goBackButton</div>
      <div
        style={{
          position: "absolute",
          top: "50%",
          left: "50%",
          transform: "translate(-50%,-50%)",
        }}
        onClick={() => {
          history.push("/");
        }}
      >
        {props.isOpen && (
          <img
            src={Logo}
            alt="logo"
            style={{ objectFit: "contain", width: "300px" }}
          />
        )}
      </div>
    </div>
  );
}

export default Header;
