import React from "react";
import { useHistory } from "react-router";

function Menu() {
  const history = useHistory();

  return (
    <div
      style={{
        width: "100vw",
        height: "100%",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        textAlign: "center",
      }}
    >
      <div
        onClick={() => {
          history.push("/artists");
        }}
        style={{
          fontSize: 50,
          padding: "2rem 0",
          backgroundColor: "#A32B5F",
          width: "100%",
          color: "white",
          margin: "1rem 0",
        }}
      >
        Artists
      </div>
      <div
        onClick={() => {
          history.push("/arts");
        }}
        style={{
          fontSize: 50,
          padding: "2rem 0",
          backgroundColor: "#A32B5F",
          width: "100%",
          color: "white",
          margin: "1rem 0",
        }}
      >
        Arts
      </div>
    </div>
  );
}

export default Menu;
