
import {
    GET_ALL_ARTS_REQUEST,
    GET_ALL_ARTS_SUCCESS,
    GET_ALL_ARTS_ERROR,

	GET_ART_REQUEST,
    GET_ART_SUCCESS,
    GET_ART_ERROR,

    CREATE_ART_REQUEST,
    CREATE_ART_SUCCESS,
    CREATE_ART_ERROR,

    EDIT_ART_REQUEST,
    EDIT_ART_SUCCESS,
    EDIT_ART_ERROR,

    DELETE_ART_REQUEST,
    DELETE_ART_SUCCESS,
    DELETE_ART_ERROR,

    CLEAR_ART_MESSAGE,


} from '../actions/artActions';

const initialState = {
    getAllArtsData:[],
    getAllArtsLoading:false,
    getAllArtsMessage:'',

	getArtData:[],
    getArtLoading:false,
    getArtMessage:'',
	
    createArtData:[],
    createArtLoading:false,
    createArtMessage:'',

    editArtData:[],
    editArtLoading:false,
    editArtMessage:'',

    deleteArtData:[],
    deleteArtLoading:false,
    deleteArtMessage:'',

};





function allActions(state = initialState, action) {
	switch (action.type) {
		case GET_ALL_ARTS_REQUEST:
			return Object.assign({}, state, {
				getAllArtsLoading: true,
			});
		case GET_ALL_ARTS_SUCCESS:
			return Object.assign({}, state, {
				getAllArtsLoading: false,
				getAllArtsData: action.payload.data
			});
            case GET_ALL_ARTS_ERROR:
                return Object.assign({}, state, {
                getAllArtsLoading: false,
                getAllArtsData: [],
				getAllArtsMessage: action.payload.message,
			});

            
		case GET_ART_REQUEST:
			return Object.assign({}, state, {
				getArtLoading: true,
			});
		case GET_ART_SUCCESS:
			return Object.assign({}, state, {
				getArtLoading: false,
				getArtData: action.payload.data
			});
		case GET_ART_ERROR:
			return Object.assign({}, state, {
				getArtLoading: false,
                getArtData: [],
				getArtMessage: action.payload.message,
			});


		case CREATE_ART_REQUEST:
			return Object.assign({}, state, {
				createArtLoading: true,
			});
		case CREATE_ART_SUCCESS:
			return Object.assign({}, state, {
				createArtLoading: false,
				createArtData: action.payload.data
			});
		case CREATE_ART_ERROR:
			return Object.assign({}, state, {
				createArtLoading: false,
                createArtData: [],
				createArtMessage: action.payload.message,
			});


		case EDIT_ART_REQUEST:
			return Object.assign({}, state, {
				editArtLoading: true,
			});
		case EDIT_ART_SUCCESS:
			return Object.assign({}, state, {
				editArtLoading: false,
				editArtData: action.payload.data
			});
		case EDIT_ART_ERROR:
			return Object.assign({}, state, {
				editArtLoading: false,
                editArtData: [],
				editArtMessage: action.payload.message,
			});


		case DELETE_ART_REQUEST:
			return Object.assign({}, state, {
				deleteArtLoading: true,
			});
		case DELETE_ART_SUCCESS:
			return Object.assign({}, state, {
				deleteArtLoading: false,
				deleteArtData: action.payload.data
			});
		case DELETE_ART_ERROR:
			return Object.assign({}, state, {
				deleteArtLoading: false,
                deleteArtData: [],
				deleteArtMessage: action.payload.message,
			});


        case CLEAR_ART_MESSAGE:
            return Object.assign({},state,{
                getAllArtsMessage:'',
                getArtMessage:'',
                createArtMessage:'',
                editArtMessage:'',
                deleteArtMessage:'',
            })


		default:
			return state;
	}
}

export default allActions;
