
import {
    GET_ALL_ARTISTS_REQUEST,
    GET_ALL_ARTISTS_SUCCESS,
    GET_ALL_ARTISTS_ERROR,

	GET_ARTIST_REQUEST,
    GET_ARTIST_SUCCESS,
    GET_ARTIST_ERROR,

    CREATE_ARTIST_REQUEST,
    CREATE_ARTIST_SUCCESS,
    CREATE_ARTIST_ERROR,

    EDIT_ARTIST_REQUEST,
    EDIT_ARTIST_SUCCESS,
    EDIT_ARTIST_ERROR,

    DELETE_ARTIST_REQUEST,
    DELETE_ARTIST_SUCCESS,
    DELETE_ARTIST_ERROR,

    CLEAR_ARTIST_MESSAGE,


} from '../actions/artistActions';

const initialState = {
    getAllArtistsData:[],
    getAllArtistsLoading:false,
    getAllArtistsMessage:'',

	getArtistData:[],
    getArtistLoading:false,
    getArtistMessage:'',
	
    createArtistData:[],
    createArtistLoading:false,
    createArtistMessage:'',

    editArtistData:[],
    editArtistLoading:false,
    editArtistMessage:'',

    deleteArtistData:[],
    deleteArtistLoading:false,
    deleteArtistMessage:'',

};





function allActions(state = initialState, action) {
	switch (action.type) {
		case GET_ALL_ARTISTS_REQUEST:
			return Object.assign({}, state, {
				getAllArtistsLoading: true,
			});
		case GET_ALL_ARTISTS_SUCCESS:
			return Object.assign({}, state, {
				getAllArtistsLoading: false,
				getAllArtistsData: action.payload.data
			});
            case GET_ALL_ARTISTS_ERROR:
                return Object.assign({}, state, {
                getAllArtistsLoading: false,
                getAllArtistsData: [],
				getAllArtistsMessage: action.payload.message,
			});

            
		case GET_ARTIST_REQUEST:
			return Object.assign({}, state, {
				getArtistLoading: true,
			});
		case GET_ARTIST_SUCCESS:
			return Object.assign({}, state, {
				getArtistLoading: false,
				getArtistData: action.payload.data
			});
		case GET_ARTIST_ERROR:
			return Object.assign({}, state, {
				getArtistLoading: false,
                getArtistData: [],
				getArtistMessage: action.payload.message,
			});


		case CREATE_ARTIST_REQUEST:
			return Object.assign({}, state, {
				createArtistLoading: true,
			});
		case CREATE_ARTIST_SUCCESS:
			return Object.assign({}, state, {
				createArtistLoading: false,
				createArtistData: action.payload.data
			});
		case CREATE_ARTIST_ERROR:
			return Object.assign({}, state, {
				createArtistLoading: false,
                createArtistData: [],
				createArtistMessage: action.payload.message,
			});


		case EDIT_ARTIST_REQUEST:
			return Object.assign({}, state, {
				editArtistLoading: true,
			});
		case EDIT_ARTIST_SUCCESS:
			return Object.assign({}, state, {
				editArtistLoading: false,
				editArtistData: action.payload.data
			});
		case EDIT_ARTIST_ERROR:
			return Object.assign({}, state, {
				editArtistLoading: false,
                editArtistData: [],
				editArtistMessage: action.payload.message,
			});


		case DELETE_ARTIST_REQUEST:
			return Object.assign({}, state, {
				deleteArtistLoading: true,
			});
		case DELETE_ARTIST_SUCCESS:
			return Object.assign({}, state, {
				deleteArtistLoading: false,
				deleteArtistData: action.payload.data
			});
		case DELETE_ARTIST_ERROR:
			return Object.assign({}, state, {
				deleteArtistLoading: false,
                deleteArtistData: [],
				deleteArtistMessage: action.payload.message,
			});


        case CLEAR_ARTIST_MESSAGE:
            return Object.assign({},state,{
                getAllArtistsMessage:'',
                getArtistMessage:'',
                createArtistMessage:'',
                editArtistMessage:'',
                deleteArtistMessage:'',
            })


		default:
			return state;
	}
}

export default allActions;
