import React from "react";

function ArtistCard(props) {
  return (
    <div style={{ padding: "2rem", fontSize: "28px" }}>
      <div>
        <img
          src={props.image}
          alt="ArtistImage"
          style={{ objectFit: "cover", width: "100%", height: "400px" }}
          onClick={() => {
            props.onClick();
          }}
        />
      </div>
      <div>{props.artist_name}</div>
    </div>
  );
}

export default ArtistCard;
