import { connect } from "react-redux";
import { Redirect, Route, Switch } from "react-router-dom";

// Components
import Home from "../../pages/Home";
import Menu from "../../pages/Menu";
import Arts from "../../pages/Arts";
import Artists from "../../pages/Artists";
import ArtistDetails from "../../pages/ArtistDetails";
import Error from "../../pages/Error";
import ArtistCreate from "../../pages/Admin/ArtistCreate";

export const WebContent = (props) => {
  return (
    <Switch>
      <Route exact path="/" render={() => <Redirect to="/home" />} />
      {/* Admin Routes */}
      <Route exact path="/admin/artists/create" component={ArtistCreate} />

      {/* Public Routes */}
      <Route exact path="/home">
        <Home setIsOpen={props.setIsOpen} />
      </Route>
      <Route exact path="/menu" component={Menu} />
      <Route exact path="/arts" component={Arts} />
      <Route exact path="/artists" component={Artists} />
      <Route exact path="/artist/:artistId" component={ArtistDetails} />

      {/* error route */}
      <Route render={() => <Redirect to="/error" />} />

      <Route exact path="/error">
        <Error />
      </Route>

      <Route>
        <Redirect to="/error" />
      </Route>
    </Switch>
  );
};

const mapStateToProps = (state) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(WebContent);
