import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { useParams } from "react-router";
import artistActions from "../redux/actions/artistActions";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";

function ArtistDetails(props) {
  const params = useParams();
  const [open, setOpen] = useState(false);
  const [selected, setSelected] = useState({});
  const handleOpen = (data) => {
    setSelected(data);
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  useEffect(() => {
    props.getSingleArtist({ artist_id: params.artistId });
  }, []);
  return (
    <>
      {props.getArtistLoading ? (
        <div>Loading</div>
      ) : (
        <div
          style={{
            width: "100%",
            height: "100%",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <div style={{ padding: "2rem 3rem" }}>
            <img
              src={props?.getArtistData?.artist_image}
              alt={props?.getArtistData?.artist_name}
              style={{ height: "300px", objectFit: "contain" }}
            />
          </div>
          <div
            style={{
              marginBottom: "2rem",
              fontSize: "38px",
              fontWeight: "bold",
            }}
          >
            {props?.getArtistData?.artist_name_ar}
          </div>
          <div
            style={{
              marginBottom: "2rem",
              fontSize: "28px",
              width: 700,
              textAlign: "end",
            }}
          >
            {props?.getArtistData?.artist_description_ar}
          </div>
          <div
            style={{
              marginBottom: "2rem",
              fontSize: "38px",
              fontWeight: "bold",
            }}
          >
            {props?.getArtistData?.artist_name}
          </div>
          <div
            style={{
              marginBottom: "4rem",
              fontSize: "28px",
              width: 700,
              textAlign: "start",
            }}
          >
            {props?.getArtistData?.artist_description}
          </div>
          <div style={{ display: "flex", width: "70%" }}>
            {props?.getArtistData?.artist_arts?.map((zone, i) => {
              return (
                <div key={i}>
                  <img
                    src={zone.art_image}
                    alt="art"
                    onClick={() => handleOpen(zone)}
                    style={{
                      width: "300px",
                      height: "300px",
                      objectFit: "cover",
                    }}
                  />
                  <div>{zone.art_name}</div>
                </div>
              );
            })}
          </div>
        </div>
      )}
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        open={open}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div
            style={{
              overflow: "auto",
              outline: "0",
              width: "100%",
              height: "100%",
              backgroundColor: "white",
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              position: "relative",
            }}
          >
            <div style={{ padding: "2rem 3rem" }}>
              <img
                src={selected?.art_image}
                alt={selected?.art_name}
                style={{ height: "700px", objectFit: "contain" }}
              />
            </div>
            <div
              style={{
                marginBottom: "2rem",
                fontSize: "38px",
                fontWeight: "bold",
              }}
            >
              {selected?.art_name}
            </div>
            <div
              style={{
                fontSize: "22px",
                padding: "0 3rem",
                alignSelf: "start",
              }}
            >
              <div>
                <strong>Medium:</strong>
                {selected?.art_medium}
              </div>
              <div>
                <strong>Year:</strong>
                {selected?.art_year}
              </div>
              <div>
                <strong>Dimensions:</strong>
                {selected?.art_dimensions}
              </div>
              <div>
                <strong>Origin:</strong>
                {selected?.art_origin}
              </div>
              {/* <div>
                <strong>Description:</strong>
                {selected?.art_description}
              </div> */}
            </div>
            <div
              style={{ position: "absolute", top: "1rem", right: "1rem" }}
              onClick={handleClose}
            >
              X
            </div>
          </div>
        </Fade>
      </Modal>
    </>
  );
}

const mapStateToProps = (state) => ({
  getArtistData: state.artist.getArtistData,
  getArtistLoading: state.artist.getArtistLoading,
});

const mapDispatchToProps = (dispatch) => ({
  getSingleArtist: (data) => {
    dispatch(artistActions.getSingleArtist(data));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(ArtistDetails);
